<template>
  <div class="min-h-screen bg-gray-50">
    <!-- Header -->
    <div class="bg-white shadow-sm border-b">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div class="flex items-center justify-between py-4">
          <div class="flex items-center space-x-4">
            <router-link 
              :to="`/courses/${courseId}`"
              class="text-gray-400 hover:text-gray-600"
            >
              ‚Üê –ù–∞–∑–∞–¥ –∫ –∫—É—Ä—Å—É
            </router-link>
            <div>
              <h1 class="text-lg font-medium text-gray-900">{{ exercise?.title }}</h1>
              <p class="text-sm text-gray-500">ü§ñ AI –£–ø—Ä–∞–∂–Ω–µ–Ω–∏–µ</p>
            </div>
          </div>
          
          <div v-if="exercise" class="flex items-center space-x-4">
            <div class="text-sm text-gray-600 flex items-center space-x-2">
              <span class="flex items-center">
                <span class="w-2 h-2 bg-yellow-500 rounded-full mr-1"></span>
                {{ getDifficultyLabel(exercise.difficulty) }}
              </span>
              <span v-if="exercise.timeLimit">‚è∞ {{ exercise.timeLimit }} –º–∏–Ω</span>
              <span v-if="exercise.maxAttempts">üéØ {{ attemptsLeft }} –ø–æ–ø—ã—Ç–æ–∫</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <!-- Loading State -->
      <div v-if="isLoading" class="flex justify-center py-12">
        <div class="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>

      <div v-else-if="exercise" class="grid grid-cols-1 lg:grid-cols-2 gap-8">
        <!-- Exercise Description -->
        <div class="space-y-6">
          <div class="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h2 class="text-xl font-semibold text-gray-900 mb-4">–ó–∞–¥–∞–Ω–∏–µ</h2>
            <div class="prose max-w-none">
              <p class="text-gray-700">{{ exercise.description }}</p>
            </div>
            
            <div class="mt-6 p-4 bg-blue-50 border border-blue-200 rounded-lg">
              <h3 class="font-medium text-blue-900 mb-2">üí° –ß—Ç–æ –Ω—É–∂–Ω–æ —Å–¥–µ–ª–∞—Ç—å:</h3>
              <div class="text-blue-800 whitespace-pre-line">{{ exercise.prompt }}</div>
            </div>
            
            <div v-if="exercise.expectedOutput" class="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg">
              <h3 class="font-medium text-green-900 mb-2">‚úÖ –û–∂–∏–¥–∞–µ–º—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç:</h3>
              <pre class="text-green-800 text-sm bg-green-100 p-2 rounded"><code>{{ exercise.expectedOutput }}</code></pre>
            </div>
          </div>

          <!-- Hints -->
          <div class="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <div class="flex items-center justify-between mb-4">
              <h3 class="text-lg font-medium text-gray-900">üí° –ü–æ–¥—Å–∫–∞–∑–∫–∏</h3>
              <button
                v-if="!showHints && exercise.hints.length > 0"
                @click="requestHint"
                :disabled="isRequestingHint || hintsUsed >= exercise.hints.length"
                class="px-3 py-1 text-sm bg-yellow-100 text-yellow-800 rounded-lg hover:bg-yellow-200 disabled:opacity-50"
              >
                {{ isRequestingHint ? '–ó–∞–≥—Ä—É–∑–∫–∞...' : '–ü–æ–ª—É—á–∏—Ç—å –ø–æ–¥—Å–∫–∞–∑–∫—É' }}
              </button>
            </div>
            
            <div v-if="showHints">
              <div 
                v-for="(hint, index) in visibleHints" 
                :key="index"
                class="mb-3 p-3 bg-yellow-50 border border-yellow-200 rounded-lg"
              >
                <div class="text-sm text-yellow-800">{{ hint }}</div>
              </div>
              
              <button
                v-if="hintsUsed < exercise.hints.length"
                @click="requestHint"
                :disabled="isRequestingHint"
                class="text-sm text-yellow-600 hover:text-yellow-700"
              >
                {{ isRequestingHint ? '–ó–∞–≥—Ä—É–∑–∫–∞...' : '–ï—â—ë –ø–æ–¥—Å–∫–∞–∑–∫–∞' }}
              </button>
            </div>
            
            <div v-else-if="exercise.hints.length === 0" class="text-gray-500 text-sm">
              –ü–æ–¥—Å–∫–∞–∑–∫–∏ –¥–ª—è —ç—Ç–æ–≥–æ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏—è –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã
            </div>
          </div>

          <!-- Previous Submissions -->
          <div v-if="submissions.length > 0" class="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h3 class="text-lg font-medium text-gray-900 mb-4">üìù –ü—Ä–µ–¥—ã–¥—É—â–∏–µ –ø–æ–ø—ã—Ç–∫–∏</h3>
            <div class="space-y-3">
              <div 
                v-for="(submission, index) in submissions.slice(-3)" 
                :key="submission.id"
                class="p-3 border border-gray-200 rounded-lg"
              >
                <div class="flex items-center justify-between mb-2">
                  <span class="text-sm font-medium">–ü–æ–ø—ã—Ç–∫–∞ #{{ submission.attemptNumber }}</span>
                  <div class="flex items-center space-x-2">
                    <span v-if="submission.score !== undefined" class="text-sm text-gray-600">
                      {{ submission.score }}%
                    </span>
                    <span 
                      :class="[
                        'px-2 py-1 rounded-full text-xs font-medium',
                        submission.isCorrect ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                      ]"
                    >
                      {{ submission.isCorrect ? '–ü—Ä–∞–≤–∏–ª—å–Ω–æ' : '–ù–µ–ø—Ä–∞–≤–∏–ª—å–Ω–æ' }}
                    </span>
                  </div>
                </div>
                
                <div v-if="submission.feedback" class="text-sm text-gray-600 bg-gray-50 p-2 rounded">
                  <strong>AI –û–±—Ä–∞—Ç–Ω–∞—è —Å–≤—è–∑—å:</strong> {{ submission.feedback }}
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Code Editor and Submission -->
        <div class="space-y-6">
          <div class="bg-white rounded-lg shadow-sm border border-gray-200">
            <div class="p-4 border-b border-gray-200">
              <div class="flex items-center justify-between">
                <h3 class="font-medium text-gray-900">üíª –í–∞—à–µ —Ä–µ—à–µ–Ω–∏–µ</h3>
                <div class="flex items-center space-x-2">
                  <button
                    @click="formatCode"
                    class="px-3 py-1 text-sm text-gray-600 hover:text-gray-900"
                  >
                    –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞—Ç—å
                  </button>
                  <button
                    @click="clearCode"
                    class="px-3 py-1 text-sm text-gray-600 hover:text-gray-900"
                  >
                    –û—á–∏—Å—Ç–∏—Ç—å
                  </button>
                </div>
              </div>
            </div>
            
            <div class="p-4">
              <div class="bg-gray-900 rounded-lg p-4">
                <textarea
                  v-model="userCode"
                  rows="20"
                  class="w-full bg-transparent text-green-400 font-mono text-sm border-none outline-none resize-none"
                  placeholder="# –í–≤–µ–¥–∏—Ç–µ –≤–∞—à –∫–æ–¥ –∑–¥–µ—Å—å..."
                ></textarea>
              </div>
            </div>
            
            <div class="p-4 border-t border-gray-200">
              <button
                @click="submitSolution"
                :disabled="!userCode.trim() || isSubmitting || attemptsLeft <= 0"
                class="w-full px-4 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed font-medium"
              >
                {{ isSubmitting ? 'AI –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –≤–∞—à–µ —Ä–µ—à–µ–Ω–∏–µ...' : '–û—Ç–ø—Ä–∞–≤–∏—Ç—å —Ä–µ—à–µ–Ω–∏–µ' }}
              </button>
              
              <div v-if="exercise.maxAttempts" class="mt-2 text-center text-sm text-gray-500">
                –û—Å—Ç–∞–ª–æ—Å—å –ø–æ–ø—ã—Ç–æ–∫: {{ attemptsLeft }}
              </div>
            </div>
          </div>

          <!-- Latest Feedback -->
          <div v-if="latestSubmission" class="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <div class="flex items-center space-x-2 mb-4">
              <span class="text-lg">ü§ñ</span>
              <h3 class="font-medium text-gray-900">AI –û–±—Ä–∞—Ç–Ω–∞—è —Å–≤—è–∑—å</h3>
              <span 
                :class="[
                  'px-2 py-1 rounded-full text-xs font-medium',
                  latestSubmission.isCorrect ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                ]"
              >
                {{ latestSubmission.isCorrect ? '–ü—Ä–∞–≤–∏–ª—å–Ω–æ' : '–ù—É–∂–Ω–æ –¥–æ—Ä–∞–±–æ—Ç–∞—Ç—å' }}
              </span>
            </div>
            
            <div class="space-y-3">
              <div v-if="latestSubmission.score !== undefined" class="text-sm">
                <span class="font-medium">–û—Ü–µ–Ω–∫–∞:</span> {{ latestSubmission.score }}%
              </div>
              
              <div v-if="latestSubmission.feedback" class="text-sm text-gray-700 bg-gray-50 p-3 rounded-lg">
                {{ latestSubmission.feedback }}
              </div>
              
              <div v-if="latestSubmission.isCorrect" class="text-center pt-4">
                <div class="text-green-600 text-lg mb-2">üéâ –û—Ç–ª–∏—á–Ω–æ!</div>
                <p class="text-sm text-gray-600 mb-4">–í—ã —É—Å–ø–µ—à–Ω–æ —Ä–µ—à–∏–ª–∏ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏–µ!</p>
                <router-link
                  :to="`/courses/${courseId}`"
                  class="inline-flex items-center px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700"
                >
                  –í–µ—Ä–Ω—É—Ç—å—Å—è –∫ –∫—É—Ä—Å—É
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { aiExercisesApi } from '@/services/api';
import type { AIExercise, AIExerciseSubmission } from '@/types';

const route = useRoute();

const courseId = computed(() => route.params.courseId as string);
const exerciseId = computed(() => route.params.exerciseId as string);

const isLoading = ref(true);
const isSubmitting = ref(false);
const isRequestingHint = ref(false);
const exercise = ref<AIExercise | null>(null);
const submissions = ref<AIExerciseSubmission[]>([]);
const userCode = ref('');
const showHints = ref(false);
const hintsUsed = ref(0);

const latestSubmission = computed(() => {
  return submissions.value[submissions.value.length - 1] || null;
});

const attemptsLeft = computed(() => {
  if (!exercise.value?.maxAttempts) return Infinity;
  return exercise.value.maxAttempts - submissions.value.length;
});

const visibleHints = computed(() => {
  if (!exercise.value) return [];
  return exercise.value.hints.slice(0, hintsUsed.value);
});

const getDifficultyLabel = (difficulty: string) => {
  const labels: Record<string, string> = {
    'easy': '–õ–µ–≥–∫–æ',
    'medium': '–°—Ä–µ–¥–Ω–µ',
    'hard': '–°–ª–æ–∂–Ω–æ'
  };
  return labels[difficulty] || difficulty;
};

const loadExercise = async () => {
  isLoading.value = true;
  
  try {
    // –í —Ä–µ–∞–ª—å–Ω–æ–º –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏ –∑–¥–µ—Å—å –±—É–¥–µ—Ç –∑–∞–≥—Ä—É–∑–∫–∞ —É–ø—Ä–∞–∂–Ω–µ–Ω–∏—è —Å API
    // –ü–æ–∫–∞ –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ
    exercise.value = {
      id: exerciseId.value,
      title: '–°–æ–∑–¥–∞–Ω–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏',
      description: '–°–æ–∑–¥–∞–π—Ç–µ —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç —Å–ø–∏—Å–æ–∫ —á–∏—Å–µ–ª –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –µ–≥–æ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–º –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é.',
      prompt: '–ù–∞–ø–∏—à–∏—Ç–µ —Ñ—É–Ω–∫—Ü–∏—é sort_numbers(numbers), –∫–æ—Ç–æ—Ä–∞—è:\n1. –ü—Ä–∏–Ω–∏–º–∞–µ—Ç —Å–ø–∏—Å–æ–∫ —á–∏—Å–µ–ª\n2. –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–æ–≤—ã–π –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫\n3. –ù–µ –∏–∑–º–µ–Ω—è–µ—Ç –∏—Å—Ö–æ–¥–Ω—ã–π —Å–ø–∏—Å–æ–∫',
      expectedOutput: '[1, 2, 3, 4, 5]',
      hints: [
        '–ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ –≤—Å—Ç—Ä–æ–µ–Ω–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é sorted() –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –Ω–æ–≤–æ–≥–æ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ —Å–ø–∏—Å–∫–∞',
        '–ù–µ –∑–∞–±—É–¥—å—Ç–µ –ø—Ä–æ return statement',
        '–ü—Ä–æ–≤–µ—Ä—å—Ç–µ, —á—Ç–æ –∏—Å—Ö–æ–¥–Ω—ã–π —Å–ø–∏—Å–æ–∫ –Ω–µ –∏–∑–º–µ–Ω—è–µ—Ç—Å—è –ø–æ—Å–ª–µ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏'
      ],
      difficulty: 'easy',
      timeLimit: 30,
      maxAttempts: 5
    };
    
    // Load previous submissions
    submissions.value = await aiExercisesApi.getExerciseSubmissions(exerciseId.value);
  } catch (error) {
    console.error('Failed to load exercise:', error);
  } finally {
    isLoading.value = false;
  }
};

const requestHint = async () => {
  if (!exercise.value || hintsUsed.value >= exercise.value.hints.length) return;
  
  isRequestingHint.value = true;
  
  try {
    // –í —Ä–µ–∞–ª—å–Ω–æ–º –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏ –∑–¥–µ—Å—å –º–æ–∂–µ—Ç –±—ã—Ç—å AI-–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –ø–æ–¥—Å–∫–∞–∑–∫–∞
    hintsUsed.value++;
    showHints.value = true;
  } catch (error) {
    console.error('Failed to get hint:', error);
  } finally {
    isRequestingHint.value = false;
  }
};

const submitSolution = async () => {
  if (!userCode.value.trim() || !exercise.value || isSubmitting.value) return;
  
  isSubmitting.value = true;
  
  try {
    const submission = await aiExercisesApi.submitExercise(exercise.value.id, userCode.value);
    submissions.value.push(submission);
  } catch (error) {
    console.error('Failed to submit solution:', error);
  } finally {
    isSubmitting.value = false;
  }
};

const formatCode = () => {
  // Basic Python code formatting (–≤ —Ä–µ–∞–ª—å–Ω–æ–º –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏ –º–æ–∂–Ω–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –±–æ–ª–µ–µ —Å–ª–æ–∂–Ω—É—é –ª–æ–≥–∏–∫—É)
  userCode.value = userCode.value
    .split('\n')
    .map(line => line.trim())
    .join('\n');
};

const clearCode = () => {
  userCode.value = '';
};

onMounted(() => {
  loadExercise();
});
</script>
